'use client';

import { useEffect, useState } from 'react';

export default function LibraryPage() {
  const [language, setLanguage] = useState('English');

  useEffect(() => {
    const selected = localStorage.getItem('selectedLanguage');
    if (selected) setLanguage(selected);
  }, []);

  return (
    <div className="min-h-screen bg-[#f7f8fa] flex flex-col text-sm font-sans">
      {/* Header */}
      <header className="flex items-center justify-between px-10 py-5 bg-white shadow-md">
        <div className="flex items-center gap-8">
          <img src="/logo.png" alt="Logo" className="h-10" />
          <div className="flex gap-4">
            <div className="flex flex-col">
              <label className="text-xs text-gray-500">I'm learning</label>
              <select className="px-3 py-1.5 border rounded-md bg-white">
                <option selected>{language}</option>
              </select>
            </div>
            <div className="flex flex-col">
              <label className="text-xs text-gray-500">I know</label>
              <select className="px-3 py-1.5 border rounded-md bg-white">
                <option selected>Ukrainian</option>
              </select>
            </div>
          </div>
        </div>

        <div className="flex items-center gap-4">
          <input
            type="text"
            placeholder="Search"
            className="w-64 px-4 py-2 border rounded-full bg-gray-100"
          />
          <img src="/user-avatar.png" className="h-10 w-10 rounded-full object-cover" alt="User" />
          <button className="text-xl">‚ñæ</button>
        </div>
      </header>

      {/* Body */}
      <div className="flex flex-1">
        {/* Sidebar */}
        <aside className="w-60 bg-[#1777c2] text-white py-12 px-6 space-y-6 text-base font-medium">
          <div className="cursor-pointer">üìÑ Profile</div>
          <div className="cursor-pointer font-bold">üìö Library</div>
          <div className="cursor-pointer">üë®‚Äçüè´ Tutor</div>
          <div className="cursor-pointer">üìä Resume</div>
          <div className="cursor-pointer">‚öôÔ∏è Settings</div>
        </aside>

        {/* Main content */}
        <main className="flex-1 px-10 py-8">
          {/* Filters */}
          <div className="flex gap-4 mb-6">
            <select className="px-4 py-2 border rounded-lg bg-white">
              <option>Genres</option>
            </select>
            <select className="px-4 py-2 border rounded-lg bg-white">
              <option>Authors</option>
            </select>
          </div>

          {/* Book cards */}
          <div className="space-y-4">
            {[...Array(5)].map((_, idx) => (
              <div
                key={idx}
                className="flex items-center justify-between bg-white p-4 rounded-xl shadow border"
              >
                <div className="flex items-center gap-4">
                  <img
                    src="/book-cover.jpg"
                    alt="Book"
                    className="w-14 h-20 object-cover rounded-md"
                  />
                  <div>
                    <h2 className="text-base font-semibold text-gray-900">
                      The Old Man and The Sea
                    </h2>
                    <p className="text-green-600 text-sm mt-1">Ernest Hemingway</p>
                    <div className="mt-2 w-48 bg-gray-200 h-2 rounded-full overflow-hidden">
                      <div className="bg-green-500 h-full w-1/2"></div>
                    </div>
                  </div>
                </div>
                <button className="text-gray-400 text-2xl hover:text-red-500">‚ô°</button>
              </div>
            ))}
          </div>

          {/* Pagination */}
          <div className="flex justify-center items-center gap-6 mt-10 text-sm text-gray-600">
            <span>Page 1 of 20</span>
            <span className="cursor-pointer text-xl">‚ü≤</span>
            <span className="cursor-pointer text-xl">‚ü≥</span>
          </div>
        </main>
      </div>

      {/* Footer */}
      <footer className="bg-[#f3f4f6] text-gray-600 px-10 py-10 text-sm grid grid-cols-3 gap-8">
        <div>
          <p>
            Lorem ipsum leo amet tristique ut urna phasellus arcu consectetur massa lectus quam
            bibendum in.
          </p>
          <div className="flex space-x-4 mt-4">
            <img src="/facebook-icon.svg" className="w-5 h-5" />
            <img src="/instagram-icon.svg" className="w-5 h-5" />
          </div>
        </div>
        <div className="space-y-2">
          <p>Help center</p>
          <p>Contact us</p>
          <p>F.A.Q.</p>
        </div>
        <div className="space-y-2">
          <p>Privacy policy</p>
          <p>Terms of service</p>
          <p>About us</p>
        </div>
      </footer>
    </div>
  );
}
